/**
 * @file 
 */

import request from 'superagent'

const defaultErrorMsg = ''

// ajax
export function ajax(params) {
    let {url, type, data, success, error} = params
    data = data || {}
    let req = null
    if (type === 'POST' || type === 'post') {
        req = request.post(url)
            .withCredentials()
            .set('Content-Type', 'application/json')
            .send(JSON.stringify(data))
    } else {
        req = request.get(url)
            .withCredentials()
            .query(data)
    }
    req.set('Accept', 'application/json').end((err, res) => {
        if (!err) {
            typeof(success) === 'function' && success(res.body)
        } else {
            typeof(error) === 'function' && error(err)
        }
    })
}

// 
function requestDone(err, res, success, error) {
    // ajax
    if (err) {
        typeof(error) === 'function' && error(defaultErrorMsg)
        return
    }
    const ret = res.body
    // 
    if (ret === null || ret === undefined || ret === '' || typeof(ret) !== 'object') {
        typeof(error) === 'function' && error(defaultErrorMsg)
        return
    }
    // err0
    if (ret.err !== 0 && ret.err !== '0') {
        typeof(error) === 'function' && error(ret.msg || defaultErrorMsg, ret.data)
        return
    }
    // 
    if (typeof(success) === 'function') {
        success(ret.data, ret.msg)
    }
}

// apiajaxerr=0
export function api(params) {
    let {url, type, data, success, error} = params
    data = data || {}
    let req = null
    if (type === 'POST' || type === 'post') {
        if (data instanceof FormData) {
            req = request.post(url).withCredentials().send(data)
        } else {
            req = request.post(url).withCredentials().type('form').send(data)
        }
    } else {
        req = request.get(url).withCredentials().query(data)
    }
    req.set('Accept', 'application/json').end((err, res) => requestDone(err, res, success, error))
}

// 
export function uploadRequest({url, data, success, error, progress}) {
    request.post(url).withCredentials().send(data).on('progress', e => {
        if (typeof(progress) === 'function') {
            progress(e)
        }
    }).end((err, res) => requestDone(err, res, success, error))
}

// apiaction
export function sampleApiAction(apiUrl, method = 'GET') {
    return (vm, params = {}) => {
        const {data, success, error} = params
        api({
            url: apiUrl,
            type: method,
            data,
            success(result, msg) {
                typeof(success) === 'function' && success(result, msg)
            },
            error(msg) {
                typeof(error) === 'function' && error(msg)
            }
        })
    }
}