/**
 * @file mixin
 */

export default {

    methods: {
        msg(content) {
            this.$message.success(content)
        },

        alert(content, callback) {
            this.$alert(content, '').then(() => {
                if (typeof(callback) === 'function') {
                    callback()
                }
            }).catch(() => {
                if (typeof(callback) === 'function') {
                    callback()
                }
            })
        },

        confirm(content, callback) {
            this.$confirm(content, '').then(() => {
                if (typeof(callback) === 'function') {
                    callback()
                }
            }).catch(() => {
            })
        },

        prompt(content, callback) {
            this.$prompt(content, '', {
                confirmButtonText: '',
                cancelButtonText: '',
                inputPattern: /.+/,
                inputErrorMessage: ''
            }).then(({value}) => {
                callback(value)
            }).catch(() => {

            })
        }
    }

}